.castGallery {
  display: grid;
  //max-width: calc(100vw - 48px);
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 16px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.castGalleryItem {
  padding: 10px;
  width: 210px;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  margin: 0 auto;
}

.info {
  font-size: 14px;
  font-weight: 400;
  color: #6b778d;
  margin-top: 8px;
}

.infoSubtitle {
  font-weight: 500;
  color: #263859;
  margin-right: 8px;
}
